import { CalendarComponent } from '@syncfusion/ej2-react-calendars';
import * as React from "react";
import './App.css';
import './Custom-style.css';

export default class App extends React.Component<{}, {}> {
    //initialize the value, min and max
    private dateValue: Date = new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate());
    private minDate: Date = new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate());
    private maxDate: Date = new Date(new Date().getFullYear(), new Date().getMonth(), (new Date().getDate()) + 30);
    


    //initialize the values
    private values: Date[] = [new Date('12/15/2021'), new Date('12/17/2021'), new Date('12/16/2021'), new Date('12/18/2021')];


    public render() {
        return <CalendarComponent id="calendar" value={this.dateValue} min={this.minDate} max={this.maxDate} isMultiSelection={true} values={this.values}/>
    }
    };
    

